<template>
  <div id="side-L_container">

    <div id="search_container">
      <input type="text" id="search" placeholder="ÌïôÍµê Ïù¥Î¶ÑÏùÑ Í≤ÄÏÉâÌï¥Î≥¥ÏÑ∏Ïöî" v-model="searchQuery">
    </div>

    <div id="school_list-container">
      <table>
        <tbody>
          <tr v-for="(school, index) in filteredSchools" :key="index">
            <td @click="showSchoolInfo(school)">{{ school.SCHUL_NM }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import { EventBus } from '../event';

export default {
  data() {
    return {
      schools: [],
      average: [],
      searchQuery: ''
    };
  },


  mounted() {
    axios.get('https://port-0-node-express-eu1k2llliadus2.sel3.cloudtype.app/data')
      .then(response => {
        this.schools = response.data;
      })
    axios.get('https://port-0-node-express-eu1k2llliadus2.sel3.cloudtype.app/data/average')
      .then(response => {
        this.average = response.data;
      })
      .catch(error => {
        console.error('Îç∞Ïù¥ÌÑ∞ Î∂àÎü¨Ïò§Í∏∞ Ïò§Î•ò:', error);
      });


  },

  computed: {
    filteredSchools() {
      return this.schools.filter(school => {
        return school.SCHUL_NM.includes(this.searchQuery);
      });
    }
  },

  methods: {
    showSchoolInfo(school) {
      const updateElementText = (elementId, text) => {
        const element = document.getElementById(elementId);
        if (element) {
          element.textContent = text;
        }
      };















      // üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ ÏùºÏ£ºÏùº Í≥ÑÏÇ∞ üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ
      const today = new Date();
      const oneWeekLater = new Date(today.getTime() + 7 * 24 * 60 * 60 * 1000);

      const formatDate = date => {
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}${month}${day}`;
      };
      const fromFormattedDate = formatDate(today);
      const toFormattedDate = formatDate(oneWeekLater);






      // üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ Í∏âÏãù Î∂àÎü¨Ïò§Í∏∞ üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ

      const Meal_apiUrl = `https://open.neis.go.kr/hub/mealServiceDietInfo?ATPT_OFCDC_SC_CODE=${school.ATPT_OFCDC_SC_CODE}&SD_SCHUL_CODE=${school.SD_SCHUL_CODE}&MLSV_FROM_YMD=${fromFormattedDate}&MLSV_TO_YMD=${toFormattedDate}&Type=json`;

      const mealContainer = document.getElementById('Meal_container');

      axios.get(Meal_apiUrl)
        .then(response => {
          const mealsData = response.data.mealServiceDietInfo[1].row;

          mealContainer.innerHTML = mealsData.map(row => `
              <div class="meal-item">
                <p>ÎÇ†Ïßú: ${row.MLSV_YMD}</p>
                <p>${row.DDISH_NM.replace(/\n/g, '<br>')}</p>
              </div>
            `).join('');
        })
        .catch(error => {
          console.error('Í∏âÏãù Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§ „Öé„Öé:', error);
          mealContainer.innerHTML = '<p id="Meal_error">Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÖÎ°úÎìúÎêòÏßÄ ÏïäÏïòÍ±∞ÎÇò,<br>Í∏âÏãùÏùÑ ÏßÄÏõêÌïòÏßÄ ÏïäÏäµÎãàÎã§.</p>';
        });



      // üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ ÏãúÍ∞ÑÌëú Î∂àÎü¨Ïò§Í∏∞ üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ



      const timetableContainer = document.getElementById('timetable_container');

      for (let grade = 1; grade <= 3; grade++) {
        const optgroup = document.querySelector(`optgroup[label="${grade}ÌïôÎÖÑ"]`);

        // ÎπÑÏö∞Í∏∞
        while (optgroup.firstChild) {
          optgroup.removeChild(optgroup.firstChild);
        }

        for (let classNum = 1; classNum <= 24; classNum++) {
          const timeTable_apiUrl = `https://open.neis.go.kr/hub/hisTimetable?ATPT_OFCDC_SC_CODE=${school.ATPT_OFCDC_SC_CODE}&SD_SCHUL_CODE=${school.SD_SCHUL_CODE}&GRADE=${grade}&CLASS_NM=${classNum}&TI_FROM_YMD=${fromFormattedDate}&TI_TO_YMD=${toFormattedDate}&Type=json`;

          axios.get(timeTable_apiUrl)
            .then(response => {
              if (response.data.hisTimetable && response.data.hisTimetable[1].row) {

                const option = document.createElement('option');
                option.value = `${grade}-${classNum}`;
                option.textContent = `${classNum}Î∞ò`;
                optgroup.appendChild(option);
              }
            })
            .catch(error => {
              console.error(`ÌïôÎÖÑ: ${grade}, Î∞ò: ${classNum} ÏãúÍ∞ÑÌëú ÏóÜÏäµÎãàÎã§ „Öé„Öé:`, error);
              timetableContainer.innerHTML = '<p id="timetable_error">Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÖÎ°úÎìúÎêòÏßÄ ÏïäÏïòÍ±∞ÎÇò,<br>Í∏âÏãùÏùÑ ÏßÄÏõêÌïòÏßÄ ÏïäÏäµÎãàÎã§.</p>'
            });
        }
      }




      // üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ ÌïôÍµê ÏùºÏ†ï Î∂àÎü¨Ïò§Í∏∞ üòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄüòÄ
      const nextMonth = new Date(today.getFullYear(), today.getMonth() + 1, today.getDate());

      const toFormattedDatee = formatDate(nextMonth);

      const Calendar_apiUrl = `https://open.neis.go.kr/hub/SchoolSchedule?ATPT_OFCDC_SC_CODE=${school.ATPT_OFCDC_SC_CODE}&SD_SCHUL_CODE=${school.SD_SCHUL_CODE}&AA_FROM_YMD=${fromFormattedDate}&AA_TO_YMD=${toFormattedDatee}&Type=json`;

      const CalendarContainer = document.getElementById('Calendar_container');

      axios.get(Calendar_apiUrl)
        .then(response => {

          const CalendarData = response.data.SchoolSchedule[1].row;

          CalendarContainer.innerHTML = CalendarData.map(item => `
      <div class="Calendar-item">
        <p>ÎÇ†Ïßú: ${item.AA_YMD}</p>
        <p>${item.EVENT_NM.replace(/\n/g, '<br>')}</p>
      </div>
    `).join('');
        })
        .catch(error => {
          console.error('ÌïôÍµê ÏùºÏ†ï Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏäµÎãàÎã§ „Öé„Öé:', error);
          CalendarContainer.innerHTML = '<p id="Calendar_error">Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÖÎ°úÎìúÎêòÏßÄ ÏïäÏïòÍ±∞ÎÇò,<br>ÏùºÏ†ïÏùÑ ÏßÄÏõêÌïòÏßÄ ÏïäÏäµÎãàÎã§.</p>';
        });







      const selectedRegionValue = school.ATPT_OFCDC_ORG_NM;
      const matchedAverageData = this.average.find(item => item.region === selectedRegionValue);

      if (matchedAverageData) {
        EventBus.updateSelectedRegionAndAverage(selectedRegionValue, matchedAverageData.averageStudents, matchedAverageData.studentsBelowAverage);
      } else {
        console.log(`Ìï¥Îãπ ÏßÄÏó≠Ïùò ÌèâÍ∑† Îç∞Ïù¥ÌÑ∞Î•º Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.`);
      }




      // ÌïôÍµêÏù¥Î¶Ñ ÎÇòÌÉÄÎÇ¥Í∏∞
      updateElementText('school_name', school.SCHUL_NM);
      // ÌïôÍµêÏúÑÏπò ÎÇòÌÉÄÎÇ¥Í∏∞
      updateElementText('School_Location', school.ORG_RDNMA);
      // ÌïôÍµêÏ¢ÖÎ•ò ÎÇòÌÉÄÎÇ¥Í∏∞
      updateElementText('School_Type', school.HS_SC_NM);


      // ÎÇ®ÎÖÄÍ≥µÌïô ÌåêÎã®ÌõÑ ÎÇòÌÉÄÎÇ¥Í∏∞
      let School_Gender_Text;

      if (school.COEDU_SC_NM === 'ÎÇ®') {
        School_Gender_Text = 'ÎÇ®ÏûêÍ≥†Îì±ÌïôÍµê';
      } else if (school.COEDU_SC_NM === 'Ïó¨') {
        School_Gender_Text = 'Ïó¨ÏûêÍ≥†Îì±ÌïôÍµê';
      } else if (school.COEDU_SC_NM === 'ÎÇ®Ïó¨Í≥µÌïô') {
        School_Gender_Text = 'ÎÇ®ÎÖÄÍ≥µÌïô';
      } else {
        School_Gender_Text = school.COEDU_SC_NM;
      }
      updateElementText('School_Gender', School_Gender_Text);

      // ÌïôÍµêÏ†ÑÎ≤à ÎÇòÌÉÄÎÇ¥Í∏∞
      updateElementText('School_Pnumber', school.ORG_TELNO);


      // Î∞îÏ∞®Ìä∏(ÌèâÍ∑†ÌïôÏÉùÏàò) ÎÇòÌÉÄÎÇ¥Í∏∞
      // const Î∞òÍ∞úÏàò = school.COL_C1 + school.COL_C2 + school.COL_C3
      // const ÌèâÍ∑†Í∞í = school.SUM / Î∞òÍ∞úÏàò


      // EventBus.updateChartData([
      //   { name: "A", value: 30 },
      //   { name: school.SCHUL_NM, value: ÌèâÍ∑†Í∞í.toFixed(1) },
      //   { name: 'C', value: 40 },
      // ]);

      // ÌïôÍµêÏ¢åÌëú Î≥¥ÎÇ¥Í∏∞
      EventBus.updatelocation(parseFloat(school.LGTUD), parseFloat(school.LTTUD));

      // Ï¥ùÌïôÏÉùÏàò Î≥¥ÎÇ¥Í∏∞
      EventBus.updateStudentnumber(school.SUM);

      // 2024ÏòàÏ∏° Îç∞Ïù¥ÌÑ∞ Î≥¥ÎÇ¥Í∏∞
      EventBus.updateAi(
        Math.floor(school.SUM2020),
        Math.floor(school.SUM2021),
        Math.floor(school.SUM2022),
        Math.floor(school.SUM2023),
        Math.floor(school.SUM2024)
      );




      // ÏÑ±Î≥ÑÎπÑÏú® Í∑∏ÎûòÌîÑ
      EventBus.updateStudentgender([
        { value: school.COL_MSUM, name: 'ÎÇ®Ïûê', itemStyle: { color: '#85B8F8' } },
        { value: school.COL_WSUM, name: 'Ïó¨Ïûê', itemStyle: { color: '#F1BFC9' } },
      ]);



      // ÌïôÍµêÌôàÌéòÏù¥ÏßÄ
      const schoolLink = school.HMPG_ADRES;
      EventBus.updateSelectedSchoolLink(schoolLink);

      // ÏûëÎÖÑÌïôÏÉùÏàò ÎπÑÍµê
      EventBus.updateLastyearSUMs(school.LastyearSUM, school.SUM);

      document.getElementById('SNS_container').innerHTML = `<a href='https://www.youtube.com/results?search_query=` + school.SCHUL_NM +`' target='_blank'>`+school.SCHUL_NM +` Ïú†ÌäúÎ∏å Î∞îÎ°úÍ∞ÄÍ∏∞</a>`
      document.getElementById('Naver_container').innerHTML = `<a href='https://search.naver.com/search.naver?where=kin&sm=tab_jum&query=` + school.SCHUL_NM +`' target='_blank'>`+school.SCHUL_NM +` ÎÑ§Ïù¥Î≤Ñ ÏßàÎ¨∏ Î∞îÎ°úÍ∞ÄÍ∏∞</a>`
      document.getElementById('insta_container').innerHTML = `<a href='https://google.com/search?q=` + school.SCHUL_NM +`' target='_blank'>`+school.SCHUL_NM +` Íµ¨Í∏Ä Î∞îÎ°úÍ∞ÄÍ∏∞</a>`


      console.log(school.SCHUL_NM)
    },
  },

};
</script>


  
  
  
<style>
#side-L_container {
  display: flex;
  flex-direction: column;
}


#search_container {
  height: 50px;
  position: relative;
  border-radius: inherit;
  background-color: #BDB7AF;
  border-radius: 20px;
}

#search_container>* {
  border: none;
  width: 470px;
  line-height: 50px;
  border-radius: 20px;
  background-color: #BDB7AF;
  padding-left: 30px;
  font-size: 20px;
}



#school_list-container {
  margin-top: 20px;
  height: 1820px;
  overflow-y: scroll;
  background-color: #BDB7AF;
  border-radius: 20px;
}

#school_list-container::-webkit-scrollbar {
  width: 10px;
  /* Ïä§ÌÅ¨Î°§Î∞îÏùò ÎÑàÎπÑ */
}

#school_list-container::-webkit-scrollbar-thumb {
  height: 30%;
  /* Ïä§ÌÅ¨Î°§Î∞îÏùò Í∏∏Ïù¥ */
  background: #000000;
  /* Ïä§ÌÅ¨Î°§Î∞îÏùò ÏÉâÏÉÅ */

  border-radius: 10px;
}

#school_list-container::-webkit-scrollbar-track {
  background: rgba(33, 122, 244, .1);
  /*Ïä§ÌÅ¨Î°§Î∞î Îí∑ Î∞∞Í≤Ω ÏÉâÏÉÅ*/
}

table {
  width: 100%;
  border-collapse: collapse;
}

th,
td {
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

tr:hover {
  background-color: #f2f2f2;
  border-radius: inherit;
}
</style>
  