<template>
    <div class="Circle-graph Parents_div" ref="chartContainer" style="position: relative;">
        <div id="Number">
            <p ref="countingElement" class="inline">{{ animatedStudentNumber }}</p>&nbsp;Î™Ö
        </div>
        <div id="Str">
            {{ selectedRegion }} ÌèâÍ∑† ÌïôÏÉù ÏàòÎ≥¥Îã§<br>
            <span
                :class="{ 'lower': studentNumber < averageStudentNumber, 'higher': studentNumber > averageStudentNumber }"
                :style="{ color: studentNumber < averageStudentNumber ? '#85B8F8' : '#F1BFC9' }"
                class="Str_sub">
                {{ Math.abs(studentNumber - averageStudentNumber) }}Î™Ö
                {{ studentNumber < averageStudentNumber ? 'Ï†ÅÏñ¥Ïöî ‚¨á' : 'ÎßéÏïÑÏöî ‚¨Ü' }} </span>
        </div>
    </div>
</template>
    
<script>
import { ref, watch } from 'vue';
import { EventBus } from '../event';

export default {
    setup() {
        const studentNumber = ref(0);
        const countingElement = ref(null);
        const selectedRegion = ref('');
        const averageStudentNumber = ref(0);

        watch(EventBus.studentNumber, (newStudentNumber) => {
            animateNumber(countingElement.value, studentNumber.value, newStudentNumber, 1000);
            studentNumber.value = newStudentNumber;
        });

        watch(EventBus.selectedRegion, (newRegion) => {
            selectedRegion.value = newRegion.replace('ÍµêÏú°Ï≤≠','');
        });

        watch(EventBus.averageStudentNumber, (newAverage) => {
            averageStudentNumber.value = newAverage;
        });

        function animateNumber(element, startNumber, ÎÅùÎ≤àÌò∏, duration) {
            const range = ÎÅùÎ≤àÌò∏ - startNumber;
            const Inc = range / (duration / 16);
            let ÌòÑÏû¨Î≤àÌò∏ = startNumber;

            const updateNumber = () => {
                ÌòÑÏû¨Î≤àÌò∏ += Inc;
                element.textContent = Math.floor(ÌòÑÏû¨Î≤àÌò∏);

                if ((Inc > 0 && ÌòÑÏû¨Î≤àÌò∏ < ÎÅùÎ≤àÌò∏) || (Inc < 0 && ÌòÑÏû¨Î≤àÌò∏ > ÎÅùÎ≤àÌò∏)) {
                    requestAnimationFrame(updateNumber);
                } else {
                    element.textContent = ÎÅùÎ≤àÌò∏;
                }
            };

            updateNumber();
        }

        return {
            studentNumber,
            countingElement,
            selectedRegion,
            averageStudentNumber,
        };
    },
};
</script>



<style>
#Number {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -40%);
    font-size: 30px;

}

#Number>p {
    display: inline;
}

#Str {
    white-space: nowrap;
    text-align: center;
    position: absolute;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.Str_sub {
    font-size: 23px;
    transition: 0.2s;
}
</style>
  

  
